:root {
    --bg-color:#191f36;
    --snd-bf-color: #262B40;
    --blue-light: rgb(0, 140, 255);
    --blue-main: #2965f1;
}

#projects {
  
    width: 100vw;
    max-width: 75rem;
    display: flex;
    flex-direction: column;  
    align-items: center;
    justify-content: center;
    text-align: center;
    margin: 0 auto;
    padding: 60px; 
    margin-bottom: 10%;
      
}

.projects h1{
    font-size: 5rem;
   
    margin-bottom: 5rem;
    padding-bottom: 0.5rem;
   
}

.projects-container {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    align-items: center;
    gap: 2rem;
    width: 80vw;
   
}

.projects-container .projects-box {
    position: relative;
    border-radius: 2rem;
    box-shadow: 0 0 1rem var(--bg-color);
    overflow: hidden;
    display: flex;
}

.projects-box img {
    width: 100%;
    transition: .5s ease;
    opacity: 0.7;
    
}

.projects-box:hover img {
    transform: scale(1.1);
}

.projects-box .projects-layer {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: linear-gradient(rgba(0, 0, 0, 0.1), var(--blue-main));
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    text-align: center;
    padding: 0 4rem;
    transform: translateY(100%);
    transition: 0.5 ease;
}

.projects-box:hover .projects-layer {
    transform: translateY(0);
}

.projects-layer h4 {
    font-size: 2rem;
    
}

.projects-layer p{
    font-size: 1.2rem;
    margin: 0.3rem 0 1rem;
}

.projects-layer a {
    display: inline-flex;
    justify-content: center;
    align-items: center;
    width: 3rem;
    height: 3rem;
    margin: 4px;   
    border: 2px solid white;
    border-radius: 50px;
}


.project-icon .icon-circle .fa-solid{
    color: white;
}


/* Responsive styles */
@media (max-width: 1200px) {
    .projects h1 {
        font-size: 4rem;
        margin-bottom: 4rem;
    }

    .projects-container {
        gap: 1.5rem;
    }

    .projects-layer h4 {
        font-size: 1.8rem;
    }

    .projects-layer p {
        font-size: 1rem;
    }
}

@media (max-width: 992px) {
    .projects-container {
        grid-template-columns: repeat(2, 1fr);
        width: 90vw;
    }

    .projects h1 {
        font-size: 3.5rem;
        margin-bottom: 3rem;
    }

    .projects-layer h4 {
        font-size: 1.6rem;
    }

    .projects-layer p {
        font-size: 1rem;
    }
}

@media (max-width: 768px) {
    #projects {
        padding: 40px;
    }

    .projects h1 {
        font-size: 3rem;
        margin-bottom: 2.5rem;
    }

    .projects-container {
        grid-template-columns: 1fr;
        width: 95vw;
    }

    .projects-layer h4 {
        font-size: 1.5rem;
    }

    .projects-layer p {
        font-size: 0.9rem;
    }

    .projects-layer a {
        width: 2.5rem;
        height: 2.5rem;
    }
}

@media (max-width: 576px) {
    #projects {
        padding: 30px;
    }

    .projects h1 {
        font-size: 2.5rem;
        margin-bottom: 2rem;
    }

    .projects-layer h4 {
        font-size: 1.4rem;
    }

    .projects-layer p {
        font-size: 0.8rem;
    }

    .projects-layer a {
        width: 2rem;
        height: 2rem;
    }

}