:root {
    --bg-color:#191f36;
    --snd-bf-color: #262B40;
    --blue-light: rgb(0, 140, 255);
    --blue-main: #2965f1;
}

#services {
  
    width: 100vw;
    max-width: 75rem;
    display: flex;
    flex-direction: column;  
    align-items: center;
    justify-content: center;
    text-align: center;
    margin: 0 auto;
    
    padding: 60px;
    
    
}

.services h1{
  font-size: 5rem;
 
  margin-bottom: 5rem;
  padding-bottom: 0.5rem;
 
}

.services h2{
font-size: 1.6rem;
}

.services-container {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    align-items: center;
    gap: 2rem;
    width: 80vw;
     
}


.services-container .services-box {
  position: relative;
  border-radius: 2rem;
  box-shadow: 0 0 1rem var(--bg-color);
  
  background-color: var(--snd-bf-color);
  padding: 4rem 2rem 4rem;
  text-align: center;
  transition: transform 0.5s ease;
  height: 240px;
  width: 100% ;

    
}

.services-container .services-box:hover {
    background-color: var(--blue-main);
    transform: scale(1.1);
}



.service-line {
    width: 60px;
    height: 3px;
    background-color: var(--blue-main); 
    margin: 0 auto;
    margin-top: 10px;
  }

  .services-container .services-box:hover .service-line{
    background-color: white;
  }

  .fa-solid {
    height: 46px;
    width: 50px;
    color:var(--blue-main);
  }

  .services-container .services-box:hover .fa-solid {color: yellow}


  /* Responsive Adjustments */

/* Tablet (992px - 1200px) */
@media (max-width: 1200px) {
  .services h1 {
      font-size: 4rem;
      margin-bottom: 4rem;
  }
  .services h2 {
      font-size: 1.4rem;
  }
  .services-container {
      gap: 1.5rem;
  }
}

/* Tablet Portrait (768px - 992px) */
@media (max-width: 992px) {
  .services h1 {
      font-size: 3.5rem;
      margin-bottom: 3rem;
  }
  .services h2 {
      font-size: 1.2rem;
  }
  .services-container {
      grid-template-columns: repeat(2, 1fr);
      gap: 1.5rem;
      width: 90vw;
  }
  .services-container .services-box {
      padding: 3rem 1.5rem;
      height: auto;
  }
}

/* Mobile Landscape (576px - 768px) */
@media (max-width: 768px) {
  .services h1 {
      font-size: 3rem;
      margin-bottom: 2.5rem;
  }
  .services h2 {
      font-size: 1.1rem;
  }
  .services-container {
      grid-template-columns: 1fr;
      gap: 1.2rem;
      width: 95vw;
  }
  #services {
      padding: 40px;
  }
  .services-container .services-box {
      padding: 2.5rem 1rem;
  }
}

/* Mobile Portrait (Up to 576px) */
@media (max-width: 576px) {
  .services h1 {
      font-size: 2.5rem;
      margin-bottom: 2rem;
  }
  .services h2 {
      font-size: 1rem;
  }
  .services-container {
      gap: 1rem;
  }
  #services {
      padding: 30px;
  }
  .services-container .services-box {
      padding: 2rem 0.8rem;
  }
}