#skills{  
      
    width: 100vw;
    max-width: 75rem;
    display: flex;
    flex-direction: column;  
    align-items: center;
    justify-content: center;
    margin: 0 auto;
    overflow: hidden;
  
}

.skills{
    width: 98%;
    position: relative;}

.skills h1{
    margin-top: 40px;
    margin-bottom: 5px;
    color: #fff;
    font-size: 5rem;
    text-align: center;
}

.skills-container{
    display: grid;
    grid-template-columns: 1fr 1fr;
    margin: 0 auto;
    align-items: center;
    gap: 24px;
    
}

.column1, .column2{
padding: 20px;
}

.skills h2{
    font-weight: 400;
    letter-spacing: 1px;
    margin-top: 40px;
    margin-bottom: 5px;
    color: #fff;
    font-size: 16px;
}

.skills p{
    font-size: 1rem;      
    text-align: center;  
   
}

.skill-item{
    margin-top: 30px;
}

.progress-bar{
    background-color: #353434;
    width: 550px; 
    height: 10px;
    border-radius: 5px;
}

.progress-bar div{
    height: 10px;
    border-radius: 5px;
    width: 0%;
    background-color: #2965f1;
}


.progress-bar div span{
    height: 60px;
    width: 60px;
    border-radius: 50%;
    background-color: rgb(30, 30, 30);
    float: right;
    margin-top: -15px;
    margin-right: -20px;
    color: #fff;
    display: flex;
    align-items: center;
    justify-content: center;
    border: 1px solid #2965f1 ;
}

