/* Responsive Adjustments */

/* Tablet (992px - 1200px) */
@media (max-width: 1200px) {
    .skills h1 {
        font-size: 4rem;
    }
    .skills-container {
        gap: 20px;
    }
    .progress-bar {
        width: 450px;
    }
}

/* Tablet Portrait (768px - 992px) */
@media screen and (max-width: 992px) {

    .skills-container{
        display: flex;
        justify-content: center;
        align-items: center;
        flex-wrap: wrap;
        gap: 0;
        margin: 0;        
    }

    .column2{
        padding-top: -10;
        margin-top: -72px;
        }

    .progress-bar{
        width: 90vw;
    }
} 

/* Mobile Landscape (576px - 768px) */
@media (max-width: 768px) {
    .skills h1 {
        font-size: 3rem;
    }
    .skills h2, .skills p {
        font-size: 0.9rem;
    }
    .progress-bar {
        width: 95w;
    }
    #skills {
        padding: 20px;
    }
}

/* Mobile Portrait (Up to 576px) */
@media (max-width: 576px) {
    .skills h1 {
        font-size: 2.5rem;
    }
    .skills h2, .skills p {
        font-size: 0.8rem;
    }
    .progress-bar {
        width: 250px;
    }
    #skills {
        padding: 15px;
    }
}