:root {
    --bg-color:#191f36;
    --snd-bf-color: #262B40;
    --blue-light: rgb(0, 140, 255);
    --blue-main: #2965f1;
}

#intro {
    height: calc(100vh - 4rem);
    width: 100vw;
    max-width: 75rem;
    margin: 0 auto;
    overflow: hidden;
    position: relative;
}



.list .item .bg {
    position: absolute;
    top: 0rem;
    right: 0;
    
    z-index: -1;
    object-fit: cover;
    height: 100vh;
}

.introContent{
    height: 100vh;
    width: 100vw;
     padding: 2rem;
    font-size: 3rem;
    font-weight: 700;
    display: flex;
    flex-direction: column;
    justify-content: center;
           
}


#intro .list .item:nth-child(1){
    z-index: 1;
    opacity: 1;
    visibility: visible;
}


#intro .list .item:nth-child(1) .hello,
#intro .list .item:nth-child(1) .Im,
#intro .list .item:nth-child(1) .introName,
#intro .list .item:nth-child(1) .intro-position,
#intro .list .item:nth-child(1) .introPara,
#intro .list .item:nth-child(1) .button-container,
#intro .list .item:nth-child(1) .bg {
    transform: translateY(50px);
    filter: blur(20px);
    opacity: 0;
    animation: showContent 0.5s 1s linear 1 forwards;
}

#intro .list .item:nth-child(1) .bg {
    animation-delay: 1s;
}
#intro .list .item:nth-child(1) .hello {
    animation-delay: 1.2s;
}

#intro .list .item:nth-child(1) .Im {
    animation-delay: 1.4s;
}

#intro .list .item:nth-child(1) .introName {
    animation-delay: 1.4s;
}

#intro .list .item:nth-child(1) .intro-position{
    animation-delay: 1.6s;
}


#intro .list .item:nth-child(1) .introPara {
    animation-delay: 1.8s;
}

#intro .list .item:nth-child(1) .button-container{
    animation-delay: 2s;
}

@keyframes showContent {
    to{
        opacity: 1;
        filter:blur(0);
        transform: translateY(0);
    }
    
}

.hello{
    font-size: 1.75rem;
    font-weight: 100;
}

.introName{
    color: rgb(0, 140, 255);
}

.button-container{
    display: flex;
    align-items: center;
    gap: 25px;
    font-size: 24px;
    font-weight: 500;
    
}
.btn-hire{
    background: var(--blue-light);
    margin: 1rem 0;
    padding: 0.6rem 1.6rem;
    border-radius: 2rem;
    border:none;
    font-size: medium;
    font-weight: normal;
}

.btn-hire:hover{
    color: white;
    cursor: pointer;
    background: var(--blue-main);
}

.btn-work{
    margin: 1rem 0;
    padding: 0.6rem 1.6rem;
    border-radius: 2rem;
    border:none;
    font-size: medium;
    font-weight: normal;
    background: rgb(194, 194, 49);
    
}

.btn-work:hover{
    color: white;
    cursor: pointer;
    background: var(--blue-main);
}

.introPara{
    font-size: medium;
    font-weight: 400;    
}

i {
    position: absolute;
    width: 4px;
    height: 4px;
    background: rgb(0, 140, 255);
    pointer-events: none;
    animation: animate 2s linear forwards;
    z-index: -5;
}

@keyframes animate{
    0% {
        opacity: 1;
        transform: translate(0,0);
    }

    100% {
        opacity: 0;
        transform: translate(var(--x), var(--y));
    }
}

@media screen and (max-width: 840px) {
    
    .list .item .bg {
        right: -10vw;
    }
        

    .introContent{
        font-size: 10vw;
        
    }

   
    .hello{
        font-size: 4.5vw;
        
    }

}  


@media screen and (max-width: 480px) {
  .list .item  .bg{
        right: -20vw;
    }

}

@media (max-width: 480px) {
    .btn-hire, .btn-work {
        padding: 0.5rem 1.2rem;
        font-size: small;
        font-weight: normal;
        margin: 0.8rem 0;
    }
}