:root {
    --bg-color:#191f36;
    --snd-bf-color: #262B40;
    --blue-light: rgb(0, 140, 255);
    --blue-main: #2965f1;
}

.navbar {
    background-color: rgb(30, 30, 30);
    height: 5rem;
    width: 100vw;
    max-width: 75rem;
    margin: 0 auto;
    padding: 1rem 2rem;
    display: flex;
    align-items: center;
    justify-content: space-between;
    position: sticky;
    top: 0;
    z-index: 3;
}

.logo {
    object-fit: cover;
    height: 3rem;
    width: 4rem;
}

.desktopMenuListItem {
    margin: 1rem;
    cursor: pointer;
}


.desktopMenuListItem:hover{
    color: rgb(0, 140, 255);
    padding-bottom: 0.5rem;
    border-bottom: 3px solid rgb(0, 140, 255);

}

.desktopMenuBtn {

    background-color: white;
    color: black;
    border: none;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0.5rem 1rem;    
    border-radius: 2rem;
}

.active {
    color: rgb(0, 140, 255);
    padding-bottom: 0.5rem;
    border-bottom: 3px solid rgb(0, 140, 255);
}

.mobMenu {
    display: none;
    object-fit: cover;
    
}
.IoMdMenu{
    height: 2rem;
    width: 2rem ;
}
.navMenu {
    position: absolute;
    top: 4rem;
    right: 2rem;
    z-index: 2;
    display: flex;
    flex-direction: column;
    padding: 0.5rem;
    height: fit-content;
    min-width: 15rem;
    background: var(--snd-bf-color) ;
    border-radius: 1rem;
}

.listItem {
    color: white;
    padding: 0.5rem 3rem;
    margin: 0.25rem;
    background: var(--bg-color);
}

@media screen and (max-width: 720px) {

    .mobMenu{
        display: flex;
    }
    .desktopMenu{
        display: none;
    }
    .desktopMenuBtn{
        display: none;
    }
}