:root {
    --bg-color:#191f36;
    --snd-bf-color: #262B40;
    --blue-light: rgb(0, 140, 255);
    --blue-main: #2965f1;
}

#about {
    
    width: 100vw;
    
    max-width: 75rem;
    display: flex;
    flex-direction: column;  
    align-items: center;
    justify-content: center;
    text-align: center;
    margin: 0 auto;
    padding: 60px;
    margin-bottom: 6%;
    margin-top: 13%;
}


.about{
    text-align: center;
    
}

.about h1{
    font-size: 5rem;
    margin-bottom: 5px;
}

.about p{
    font-size: 1.75rem;    
    max-width: 55ch;
    margin-inline: auto;
}

.about span{
    color: hsl(0 0% 100% / 0.2);
    background-clip: text;
    background-repeat: no-repeat;
    background-size: 0% 100%;
    background-image: linear-gradient(90deg, white, white);
    animation: scroll-reveal linear forwards;
    animation-timeline: view();
}

.about h1 span {
    animation-range-start: cover 20vh;
    animation-range-end: cover 30vh;
}

.about p span {
    animation-range-start: cover 20vh;
    animation-range-end: cover 50vh;
}

@keyframes scroll-reveal {
    to {
        background-size: 100% 100%;
    }
}

.btn-cv{
    background: var(--blue-light);
    cursor: pointer;
    margin: 2rem 0;
    padding: 0.6rem 1.6rem;
    border-radius: 2rem;
    border:none;
    font-size: large;
    font-weight:bold;
}

.btn-cv:hover {
    background: var(--blue-main);
    color: white;
}

/* Tablet (992px - 1200px) */
@media (max-width: 1200px) {
    .about h1 {
        font-size: 4rem;
    }

    .about p {
        font-size: 1.5rem;
    }

    #about {
        padding: 50px;
    }
}

/* Tablet Portrait (768px - 992px) */
@media (max-width: 992px) {
    .about h1 {
        font-size: 3.5rem;
    }

    .about p {
        font-size: 1.3rem;
    }

    #about {
        padding: 40px;
        text-align: center;
    }
}

/* Mobile Landscape (576px - 768px) */
@media (max-width: 768px) {
    .about h1 {
        font-size: 3rem;
    }

    .about p {
        font-size: 1.2rem;
    }

    #about {
        padding: 30px;
    }

    .btn-cv {
        font-size: medium;
        padding: 0.5rem 1.4rem;
    }
}

/* Mobile Portrait (Up to 576px) */
@media (max-width: 576px) {
    .about h1 {
        font-size: 2.5rem;
    }

    .about p {
        font-size: 1rem;
    }

    #about {
        padding: 20px;
        margin-top: 10%;
        margin-bottom: 5%;
    }

    .btn-cv {
        font-size: small;
        padding: 0.4rem 1.2rem;
        margin: 1.5rem 0;
    }
}