:root {
    --bg-color:#191f36;
    --snd-bf-color: #262B40;
    --blue-light: rgb(0, 140, 255);
    --blue-main: #2965f1;
}

#contact {
    
    width: 100vw;

    max-width: 75rem;
    display: flex;
    flex-direction: column;  
    align-items: center;
    justify-content: center;
    text-align: center;
    margin: 0 auto;
    padding: 60px;
    margin-bottom: 5%;
}

.contact h1{
    font-size: 5rem;
    margin-bottom: 5px;
}

.contact form {
    max-width: 70rem;
    margin: 1rem auto;
    text-align: center;
    margin-bottom: 3rem;
}

.contact form .input-box{
 display: flex;  
 justify-content: space-between;
 flex-wrap: wrap;
}

.contact form .input-box input, .contact form textarea {
    width: 100%;
    padding: 1.5rem;
    font-size: 1.6rem;
    color: white;
    background: var(--snd-bf-color);
    border: none;
    border-radius: 0.8rem;
    margin: 0.7rem 0;
}

.contact form .input-box input {
    width: 49%;
}

.contact form textarea {
    resize: none;
}

.contact form .submitBtn {
    margin-top: 2rem;
    cursor: pointer;
}

.input-box input:focus{
    outline: 2px solid var(--blue-light);
  }
.contact textarea:focus {
    outline: 2px solid var(--blue-light);
}
.submitBtn {
    
    border: none;
    background: var(--blue-light);
    margin: 1rem 0;
    padding: 0.6rem 1.6rem;
    border-radius: 2rem;
    border:none;
    font-size: medium;
    font-weight: normal;
}

.submitBtn:hover{
    background: var(--blue-main);
}
.social-links a {
    display: inline-flex;
    justify-content: center;
    align-items: center;
    width: 4rem;
    height: 4rem;
    margin: 4px;  
    background: #293151; 
    border: 2px solid white;
    border-radius: 50px;
    gap: 10%;
}

.social-links  .fa-solid{
    height: 40px;
    width: 40px;
    color: white;
}

/* Tablet (992px - 1200px) */
@media (max-width: 1200px) {
    .contact h1 {
        font-size: 4rem;
    }

    .contact form .input-box input, 
    .contact form textarea {
        font-size: 1.4rem;
    }

    #contact {
        padding: 50px;
    }
}

/* Tablet Portrait (768px - 992px) */
@media (max-width: 992px) {
    .contact h1 {
        font-size: 3.5rem;
    }

    .contact form .input-box input,
    .contact form textarea {
        font-size: 1.3rem;
    }

    .contact form .input-box {
        flex-direction: column;
    }

    .contact form .input-box input {
        width: 100%;
    }

    #contact {
        padding: 40px;
    }
}

/* Mobile Landscape (576px - 768px) */
@media (max-width: 768px) {
    .contact h1 {
        font-size: 3rem;
    }

    .contact form .input-box input, 
    .contact form textarea {
        font-size: 1.2rem;
        padding: 1.2rem;
    }

    .submitBtn {
        font-size: medium;
        padding: 0.5rem 1.4rem;
    }

    #contact {
        padding: 30px;
    }
}

/* Mobile Portrait (Up to 576px) */
@media (max-width: 576px) {
    .contact h1 {
        font-size: 2.5rem;
    }

    .contact form .input-box input, 
    .contact form textarea {
        font-size: 1rem;
        padding: 1rem;
    }

    #contact {
        padding: 20px;
    }

    .submitBtn {
        font-size: small;
        padding: 0.4rem 1.2rem;
        margin: 1.5rem 0;
    }

    .social-links a {
        width: 3rem;
        height: 3rem;
    }

    .social-links .fa-solid {
        height: 30px;
        width: 30px;
    }
}